.s-multipass {
  *:focus-visible {
    outline: 2px solid rgba(#fff, 0.9) !important; } }
.multipass {
  display: flex;
  padding-left: var(--title-spacing);
  margin: 60px 0 0;
  align-items: center;

  @include md {
    flex-direction: column;
    padding-left: 0; }
  @include sm {

    margin: 50px 0 0; }

  &__left {
    display: flex;
    flex: 1 1 28%;
    width: 28%;
    padding: 0 20px 0 0px;
    align-items: center;
    @include xl {
      padding: 0 20px 0 0; }
    @include md {
      width: 100%;
      align-items: flex-start;
      justify-content: center;
      margin: 0 0 30px;
      padding: 0; }
    a {
      @include bold;
      text-transform: uppercase;
      color: $themeColor; } }

  &__right {
    display: flex;
    flex: 1 1 72%;
    width: 72%;
    align-items: center;
    @include md {
      width: 100%; }
    @include sm {
      flex-direction: column; } }

  &__desc {
    @include md {
      text-align: center; } }

  &__text {
    font-size: 26px;
    margin: 0 0 16px; }

  &__pass {
    background: #fffc00;
    color: #000; }

  .icon-equal {
    flex: 0 0 20px;
    width: 20px;
    height: 20px;
    fill: #fff;
    margin: 0 14px 0 14px;

    @include md {
      margin: 20px 0 6px; } } }

.multipass-list {
  width: 100%;
  padding: 15px 0;

  a {
    @include bold;
    text-decoration: none;
    font-size: 16px;
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    z-index: 2;
    color: $themeColor;
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0;
    transition: all ease .2s;

    &:hover, &:focus-visible {

      text-decoration: underline;
      opacity: 1;
      & ~ picture  img {
        opacity: 0.2; } } }

  &__item {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 145px;
    background: rgba(255, 255, 255, .05);
    border-radius: 5px;
    padding: 20px;
    @include xs {
      height: 100px; }
    img {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: contain;
      opacity: 1;
      transition: all ease .2s; }

    .icon-check {
      position: absolute;
      right: -2px;
      top: -14px;
      display: block;
      width: 33px;
      height: 33px; }

    .icon-plus {
      position: absolute;
      left: -26px;
      top: 50%;
      transform: translateY(-50%);
      display: block;
      content: '';
      width: 22px;
      height: 22px;
      opacity: 1;
      fill: #fff;
      transition: all ease .2s; }

    &.swiper-slide {
      visibility: hidden; }
    &.swiper-slide-next,
    &.swiper-slide-prev,
    &.swiper-slide-next ~ .swiper-slide,
    &.swiper-slide-active {
      visibility: visible; }
    &.swiper-slide-active {
      .icon-plus {
        opacity: 0; } } } }

.visa-card {
  // width: 364px
  height: 145px;
  border-radius: 5px;
  display: flex;
  align-items: center;
  background: #fffc00;
  padding: 0 22px;
  margin: 0;
  color: #000;
  text-align: left;

  &__img {
    margin: 0 10px 0 0;

    .icon-face {
      width: 48px;
      height: 62px;
      color: #000; } }

  &__desc {

    h3 {
      @include bold;
      white-space: nowrap;
      font-size: 16px;
      text-transform: uppercase;
      line-height: 1.2;
      margin: 0 0 2px; }

    p {
      font-size: 13px;
      margin: 0;
      line-height: 1.36; } } }
